<template>
  <div id="app">
    <v-header :seller="seller"></v-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评分</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
// 可以使用webpack定义路径 路径的别名alias /build/webpack.base.conf.js
import header from '@/components/header/header.vue'
const response = require('./common/data/seller.json')
export default {
  name: 'app',
  data () {
    return {
      seller: {
        // avatar: 'https://fuss10.elemecdn.com/5/42/e744f6bff5c8271c89503f5a42ce6jpeg.jpeg?imageMogr2/thumbnail/140x140/format/webp/quality/85'
        id: 555409
      }
    }
  },
  // 组件生成时
  created () {
    // 来自请求
    this.seller = Object.assign({}, this.seller, response.data)
  },
  // 组件挂载时
  // mounted () {},
  components: {
    'v-header': header
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import './common/stylus/mixin.styl'
  .tab
    display flex
    width 100%
    height 40px
    border-1px(rgba(7,17,27,0.1))
  .tab-item
    flex 1
    text-align center
    & > a
    display block
    font-size 14px
    color rgb(77, 85, 93)
    &.active
      color rgb(240, 20, 20)
</style>



